.header {
  display: flex;
  height: 4rem;
  padding: 0.8em 1em;
  background: black;
  color: #fff;
}

.logo {
  display: flex;
  align-items: center;
  margin-right: 1em;
  cursor: pointer;
}

.title {
  margin-left: 0.2em;
  margin-top: 0.6em;
}

.input {
  flex-basis: 100%;
  font-size: 1.2rem;
  outline: 0;
}

.button {
  background-color: darkgray;
  outline: 0;
  border: none;
  cursor: pointer;
}

.buttonImg {
  padding: 0.5em 0.2em;
  height: 100%;
}

